"use client";

import { createContext, useContext, useState, ReactNode } from "react";
import { sampleItems, type Item } from "./items";

export type CharacterClass = {
  name: string;
  description: string;
  baseHealth: number;
  baseMana: number;
  baseStrength: number;
  baseDexterity: number;
  baseVitality: number;
  baseEnergy: number;
};

export interface GameState {
  character: CharacterClass | null;
  inventory: Item[];
  health: number;
  mana: number;
  level: number;
  experience: number;
  setCharacter: (char: CharacterClass) => void;
  addItem: (item: Item) => void;
  removeItem: (index: number) => void;
  updateHealth: (newHealth: number) => void;
  updateMana: (newMana: number) => void;
}

const GameStateContext = createContext<GameState | undefined>(undefined);

export function GameStateProvider({ children }: { children: ReactNode }) {
  const [character, setCharacterState] = useState<CharacterClass | null>(null);
  const [inventory, setInventory] = useState<Item[]>(sampleItems);
  const [health, setHealth] = useState(100);
  const [mana, setMana] = useState(50);
  const [level, setLevel] = useState(1);
  const [experience, setExperience] = useState(0);

  const setCharacter = (char: CharacterClass) => {
    setCharacterState(char);
    setHealth(char.baseHealth);
    setMana(char.baseMana);
  };

  const addItem = (item: Item) => {
    setInventory((prev) => [...prev, item]);
  };

  const removeItem = (index: number) => {
    setInventory((prev) => prev.filter((_, i) => i !== index));
  };

  const updateHealth = (newHealth: number) => {
    setHealth(Math.max(0, Math.min(newHealth, character?.baseHealth || 100)));
  };

  const updateMana = (newMana: number) => {
    setMana(Math.max(0, Math.min(newMana, character?.baseMana || 50)));
  };

  return (
    <GameStateContext.Provider 
      value={{ 
        character, 
        inventory, 
        health, 
        mana, 
        level, 
        experience,
        setCharacter, 
        addItem, 
        removeItem,
        updateHealth,
        updateMana
      }}
    >
      {children}
    </GameStateContext.Provider>
  );
}

export function useGameState() {
  const context = useContext(GameStateContext);
  if (context === undefined) {
    throw new Error("useGameState must be used within a GameStateProvider");
  }
  return context;
}
